'use client'

import { useState } from 'react'
import { useForm } from 'react-hook-form'
import { zodResolver } from '@hookform/resolvers/zod'
import * as z from 'zod'
import { Button } from '@/components/ui/button'
import {
  Form,
  FormControl,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from '@/components/ui/form'
import { Input } from '@/components/ui/input'
import { Textarea } from '@/components/ui/textarea'
import { Checkbox } from '@/components/ui/checkbox'
import { ImageUpload } from '@/components/ui/image-upload'
import { RENTAL_PLANS, CoworkingSpaceFields, FacilityFormProps } from '../types'

const formSchema = z.object({
  name: z.string().min(1, 'Name is required'),
  description: z.string().min(1, 'Description is required'),
  images: z.array(z.string()).min(1, 'At least one image is required'),
  videoLink: z.string().optional(),
  rentalPlans: z.array(z.enum(RENTAL_PLANS)).min(1, 'At least one rental plan is required'),
  totalSeats: z.number().min(1, 'Must have at least 1 seat'),
  availableSeats: z.number().min(0),
  rentPerYear: z.number().optional(),
  rentPerMonth: z.number().optional(),
  rentPerWeek: z.number().optional(),
  dayPassRent: z.number().optional(),
})

export function CoworkingSpacesForm({ onSubmit, onChange }: FacilityFormProps) {
  const [images, setImages] = useState<string[]>([])

  const form = useForm<CoworkingSpaceFields>({
    resolver: zodResolver(formSchema),
    defaultValues: {
      name: '',
      description: '',
      images: [],
      videoLink: '',
      rentalPlans: [],
      totalSeats: 0,
      availableSeats: 0,
    },
  })

  const selectedRentalPlans = form.watch('rentalPlans') || []

  const handleFormSubmit = async (values: CoworkingSpaceFields) => {
    try {
      console.log('Form submission started')
      console.log('Current form values:', values)
      
      // Validate required fields
      if (images.length === 0) {
        console.log('Image validation failed - no images')
        form.setError('root', { 
          type: 'manual',
          message: 'At least one image is required'
        })
        return
      }

      // Validate rental plans and rent values
      const selectedRentalPlans = values.rentalPlans || []
      console.log('Selected rental plans:', selectedRentalPlans)
      
      if (selectedRentalPlans.length === 0) {
        console.log('Rental plan validation failed - no plans selected')
        form.setError('rentalPlans', {
          type: 'manual',
          message: 'At least one rental plan is required'
        })
        return
      }

      // Check rent values for selected plans
      for (const plan of selectedRentalPlans) {
        console.log('Checking rent for plan:', plan)
        
        if (plan === 'Annual') {
          const rentPerYear = Number(values.rentPerYear)
          console.log('Annual rent value:', rentPerYear)
          if (!rentPerYear || rentPerYear <= 0) {
            console.log('Annual rent validation failed')
            form.setError('rentPerYear', {
              type: 'manual',
              message: 'Annual rent must be greater than 0'
            })
            return
          }
        }
        if (plan === 'Monthly') {
          const rentPerMonth = Number(values.rentPerMonth)
          console.log('Monthly rent value:', rentPerMonth)
          if (!rentPerMonth || rentPerMonth <= 0) {
            console.log('Monthly rent validation failed')
            form.setError('rentPerMonth', {
              type: 'manual',
              message: 'Monthly rent must be greater than 0'
            })
            return
          }
        }
        if (plan === 'Weekly') {
          const rentPerWeek = Number(values.rentPerWeek)
          console.log('Weekly rent value:', rentPerWeek)
          if (!rentPerWeek || rentPerWeek <= 0) {
            console.log('Weekly rent validation failed')
            form.setError('rentPerWeek', {
              type: 'manual',
              message: 'Weekly rent must be greater than 0'
            })
            return
          }
        }
        if (plan === 'One Day (24 Hours)') {
          const dayPassRent = Number(values.dayPassRent)
          console.log('Day pass rent value:', dayPassRent)
          if (!dayPassRent || dayPassRent <= 0) {
            console.log('Day pass rent validation failed')
            form.setError('dayPassRent', {
              type: 'manual',
              message: 'Day pass rent must be greater than 0'
            })
            return
          }
        }
      }

      // Prepare form data for submission
      const formData = {
        ...values,
        images
      }
      
      console.log('Preparing to submit form data:', formData)
      await onSubmit(formData)
      console.log('Form submitted successfully')
      
      // Reset form after successful submission
      form.reset()
      setImages([])
    } catch (error) {
      console.error('Form submission error:', error)
      form.setError('root', { 
        type: 'manual',
        message: error instanceof Error ? error.message : 'Failed to submit form'
      })
    }
  }

  return (
    <Form {...form}>
      <form onSubmit={form.handleSubmit(handleFormSubmit)} className="space-y-8">
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <FormField
            control={form.control}
            name="name"
            render={({ field }) => (
              <FormItem>
                <FormLabel>Facility Name *</FormLabel>
                <FormControl>
                  <Input {...field} />
                </FormControl>
                <FormMessage />
              </FormItem>
            )}
          />
          <FormField
            control={form.control}
            name="description"
            render={({ field }) => (
              <FormItem>
                <FormLabel>Description *</FormLabel>
                <FormControl>
                  <Textarea {...field} />
                </FormControl>
                <FormMessage />
              </FormItem>
            )}
          />
        </div>

        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <FormField
            control={form.control}
            name="totalSeats"
            render={({ field }) => (
              <FormItem>
                <FormLabel>Total No. of Coworking Seaters available *</FormLabel>
                <FormControl>
                  <Input 
                    type="number" 
                    min={0}
                    {...field} 
                    onChange={e => {
                      const value = e.target.value === '' ? 0 : Math.max(0, Number(e.target.value))
                      field.onChange(value)
                    }} 
                    value={field.value || ''}
                  />
                </FormControl>
                <FormMessage />
              </FormItem>
            )}
          />
          <FormField
            control={form.control}
            name="availableSeats"
            render={({ field }) => (
              <FormItem>
                <FormLabel>No. of seats currently available for occupancy *</FormLabel>
                <FormControl>
                  <Input 
                    type="number" 
                    min={0}
                    {...field} 
                    onChange={e => {
                      const value = e.target.value === '' ? 0 : Math.max(0, Number(e.target.value))
                      field.onChange(value)
                    }} 
                    value={field.value || ''}
                  />
                </FormControl>
                <FormMessage />
              </FormItem>
            )}
          />
        </div>

        <div className="space-y-4">
          <FormLabel>Kindly upload the photos of your facility *</FormLabel>
          <FormControl>
            <ImageUpload
              value={images}
              onChange={setImages}
              onRemove={(url) => setImages(images.filter((val) => val !== url))}
            />
          </FormControl>
          {form.formState.errors.root && (
            <p className="text-sm text-destructive">{form.formState.errors.root.message}</p>
          )}
        </div>

        <FormField
          control={form.control}
          name="videoLink"
          render={({ field }) => (
            <FormItem>
              <FormLabel>If available, kindly attach Video Link for your Facility</FormLabel>
              <FormControl>
                <Input type="url" placeholder="Google Drive / Youtube link" {...field} />
              </FormControl>
              <FormMessage />
            </FormItem>
          )}
        />

        <FormField
          control={form.control}
          name="rentalPlans"
          render={({ field }) => (
            <FormItem>
              <FormLabel>Rental Subscription Plan(s) Available *</FormLabel>
              <FormControl>
                <div className="space-y-2">
                  {RENTAL_PLANS.map((plan) => (
                    <div key={plan} className="flex items-center space-x-2">
                      <Checkbox
                        checked={field.value?.includes(plan)}
                        onCheckedChange={(checked) => {
                          const updatedPlans = checked
                            ? [...(field.value || []), plan]
                            : field.value?.filter((p) => p !== plan) || []
                          field.onChange(updatedPlans)
                          
                          if (!checked) {
                            switch (plan) {
                              case 'Annual':
                                form.setValue('rentPerYear', undefined)
                                break
                              case 'Monthly':
                                form.setValue('rentPerMonth', undefined)
                                break
                              case 'Weekly':
                                form.setValue('rentPerWeek', undefined)
                                break
                              case 'One Day (24 Hours)':
                                form.setValue('dayPassRent', undefined)
                                break
                            }
                          }
                        }}
                      />
                      <label className="text-sm font-medium leading-none peer-disabled:cursor-not-allowed peer-disabled:opacity-70">
                        {plan}
                      </label>
                    </div>
                  ))}
                </div>
              </FormControl>
              <FormMessage />
            </FormItem>
          )}
        />

        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          {selectedRentalPlans.includes('Annual') && (
            <FormField
              control={form.control}
              name="rentPerYear"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>Rent per Year (in Rupees) *</FormLabel>
                  <FormControl>
                    <Input 
                      type="number" 
                      min={0}
                      {...field} 
                      onChange={e => {
                        const value = e.target.value === '' ? 0 : Math.max(0, Number(e.target.value))
                        field.onChange(value)
                      }} 
                      value={field.value || ''}
                    />
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />
          )}
          {selectedRentalPlans.includes('Monthly') && (
            <FormField
              control={form.control}
              name="rentPerMonth"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>Rent per Month (in Rupees) *</FormLabel>
                  <FormControl>
                    <Input 
                      type="number" 
                      min={0}
                      {...field} 
                      onChange={e => {
                        const value = e.target.value === '' ? 0 : Math.max(0, Number(e.target.value))
                        field.onChange(value)
                      }} 
                      value={field.value || ''}
                    />
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />
          )}
          {selectedRentalPlans.includes('Weekly') && (
            <FormField
              control={form.control}
              name="rentPerWeek"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>Rent per Week (in Rupees) *</FormLabel>
                  <FormControl>
                    <Input 
                      type="number" 
                      min={0}
                      {...field} 
                      onChange={e => {
                        const value = e.target.value === '' ? 0 : Math.max(0, Number(e.target.value))
                        field.onChange(value)
                      }} 
                      value={field.value || ''}
                    />
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />
          )}
          {selectedRentalPlans.includes('One Day (24 Hours)') && (
            <FormField
              control={form.control}
              name="dayPassRent"
              render={({ field }) => (
                <FormItem>
                  <FormLabel>Day Pass Rent (1 Day) (in Rupees) *</FormLabel>
                  <FormControl>
                    <Input 
                      type="number" 
                      min={0}
                      {...field} 
                      onChange={e => {
                        const value = e.target.value === '' ? 0 : Math.max(0, Number(e.target.value))
                        field.onChange(value)
                      }} 
                      value={field.value || ''}
                    />
                  </FormControl>
                  <FormMessage />
                </FormItem>
              )}
            />
          )}
        </div>

        <div className="flex justify-end">
          <Button 
            type="submit" 
            className="w-full md:w-auto bg-primary hover:bg-primary/90"
            disabled={form.formState.isSubmitting}
          >
            {form.formState.isSubmitting ? (
              <>
                <span className="mr-2">Submitting...</span>
                <span className="animate-spin">⏳</span>
              </>
            ) : (
              'Submit for Approval'
            )}
          </Button>
        </div>
      </form>
    </Form>
  )
} 